import HeaderBar from "./shared/HeaderBar.vue";
import NcSelect from '@nextcloud/vue/components/NcSelect';
import { ACCOUNT_SETTING_PROPERTY_ENUM, ACCOUNT_SETTING_PROPERTY_READABLE_ENUM } from "../../constants/AccountPropertyConstants";
import { getDayNames, getFirstDay } from '@nextcloud/l10n';
import { savePrimaryAccountProperty } from "../../service/PersonalInfo/PersonalInfoService";
import { handleError } from "../../utils/handlers.ts";
import { loadState } from '@nextcloud/initial-state';
const {
  firstDayOfWeek
} = loadState('settings', 'personalInfoParameters', {});
export default {
  name: 'FirstDayOfWeekSection',
  components: {
    HeaderBar,
    NcSelect
  },
  data() {
    let firstDay = -1;
    if (firstDayOfWeek) {
      firstDay = parseInt(firstDayOfWeek);
    }
    return {
      firstDay
    };
  },
  computed: {
    inputId() {
      return 'account-property-fdow';
    },
    propertyReadable() {
      return ACCOUNT_SETTING_PROPERTY_READABLE_ENUM.FIRST_DAY_OF_WEEK;
    },
    dayOptions() {
      const options = [{
        value: -1,
        label: t('settings', 'Derived from your locale ({weekDayName})', {
          weekDayName: getDayNames()[getFirstDay()]
        })
      }];
      for (const [index, dayName] of getDayNames().entries()) {
        options.push({
          value: index,
          label: dayName
        });
      }
      return options;
    },
    valueOption() {
      return this.dayOptions.find(option => option.value === this.firstDay);
    }
  },
  methods: {
    async updateFirstDayOfWeek(option) {
      try {
        const responseData = await savePrimaryAccountProperty(ACCOUNT_SETTING_PROPERTY_ENUM.FIRST_DAY_OF_WEEK, option.value.toString());
        this.handleResponse({
          value: option.value,
          status: responseData.ocs?.meta?.status
        });
        window.location.reload();
      } catch (e) {
        this.handleResponse({
          errorMessage: t('settings', 'Unable to update first day of week'),
          error: e
        });
      }
    },
    handleResponse(_ref) {
      let {
        value,
        status,
        errorMessage,
        error
      } = _ref;
      if (status === 'ok') {
        this.firstDay = value;
      } else {
        this.$emit('update:value', this.firstDay);
        handleError(error, errorMessage);
      }
    }
  }
};