import { loadState } from '@nextcloud/initial-state';
import { t } from '@nextcloud/l10n';
import NcProgressBar from '@nextcloud/vue/components/NcProgressBar';
import Account from 'vue-material-design-icons/AccountOutline.vue';
import CircleSlice from 'vue-material-design-icons/CircleSlice3.vue';
import HeaderBar from "./shared/HeaderBar.vue";

/** SYNC to be kept in sync with `lib/public/Files/FileInfo.php` */
const SPACE_UNLIMITED = -3;
const {
  groups,
  quota,
  totalSpace,
  usage,
  usageRelative
} = loadState('settings', 'personalInfoParameters', {});
export default {
  name: 'DetailsSection',
  components: {
    Account,
    CircleSlice,
    HeaderBar,
    NcProgressBar
  },
  data() {
    return {
      groups,
      usageRelative
    };
  },
  computed: {
    quotaText() {
      if (quota === SPACE_UNLIMITED) {
        return t('settings', 'You are using {s}{usage}{/s}', {
          usage,
          s: '<strong>',
          '/s': '</strong>'
        }, undefined, {
          escape: false
        });
      }
      return t('settings', 'You are using {s}{usage}{/s} of {s}{totalSpace}{/s} ({s}{usageRelative}%{/s})', {
        usage,
        totalSpace,
        usageRelative,
        s: '<strong>',
        '/s': '</strong>'
      }, undefined, {
        escape: false
      });
    }
  }
};